
.form {
    display: flex;
    flex-direction: column;
}

.form_label {
    font-family: 'Nunito', sans-serif;
    font-size: 20px;
    font-weight: bold;
    color: white;
    text-align: center;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.form_input {
    appearance: none;
    border: none;
    outline: none;
    border-bottom: .2em solid #E91E63;
    background: rgba(233, 30, 99, 0.8);
    border-radius: .2em .2em 0 0;
    padding: .4em;
}

.form_textarea {
    font-family: 'Nunito', sans-serif;
    font-size: 16px;
    letter-spacing: 1px;
    appearance: none;
    border: none;
    outline: none;
    border-bottom: .2em solid #E91E63;
    border-radius: .2em .2em 0 0;
    padding: .4em;
    resize: none;
}